import React from 'react';
import SelectBox from '../../../../../components/UI/Atoms/Select/SelectBox';

const InviteTypeSelect = ({ title, children, src, ...props }) => {
  return (
    <SelectBox style={{ border: '1px solid #e7e7e7', backgroundColor: '#f2f2f2' }} {...props}>
      <div className="text-[#555555] flex flex-col justify-center items-center gap-[15px]">
        {title}
        <div
          style={{
            backgroundImage: `url(${src})`,
            backgroundPosition: 'center',
            backgroundRepeat: 'no-repeat',
            backgroundSize: 'contain',
            width: '195px',
            height: '200px',
            marginTop: '0',
          }}
        ></div>
      </div>
      <div className="bg-[#ffffff] w-[373px] h-[78px] text-center rounded-b-[10px] p-[20px] mt-0 tracking-normal">
        {children}
      </div>
    </SelectBox>
  );
};

export default InviteTypeSelect;
