import * as React from 'react';
import { styled } from '@mui/system';
import { ButtonBase } from '@mui/material';

const StyleBtn = styled(ButtonBase)(({ theme }) => ({
  minWidth: '70px',
  width: theme.button.common.width.md,
  height: theme.button.common.height.md,
  padding: theme.button.common.paddingSize.md,
  fontSize: theme.font.size.m,
  fontWeight: theme.font.weight.bold,
  color: theme.button.modal.color,
  background: theme.button.modal.background,
  border: `1px solid ${theme.button.modal.border}`,
  borderRadius: theme.button.common.radius,
  transition: 'all 0.3s ease',
  // boxShadow: theme.button.common.shadow.main,
  whiteSpace: 'nowrap',
  '&:hover, &:active': {
    transform: 'scale(0.95)',
    boxShadow: theme.button.common.shadow.none,
    borderColor: theme.button.modal.hover.border,
    background: theme.button.modal.hover.background,
  },
  '&:disabled': {
    background: theme.button.modal.disabled.background,
    color: theme.button.modal.disabled.color,
    borderColor: theme.button.common.disabled.border,
    boxShadow: theme.button.common.shadow.none,
  },

}));

const EditBtnMD = React.forwardRef((props, ref) => {
  return (
    <>
      <StyleBtn ref={ref} {...props} />
    </>
  );
});

export default EditBtnMD;