import React from 'react'
import { Box as MUIBox, Typography } from '@mui/material';

const style = {
  bgcolor: '#ffffff',
  borderRadius: '5px',
  mt: 1,
  mb: 1,
  pt: '20px',
  px: '20px',
  pb: '20px',
};

const Box = ({ title, btn, children, ...props }) => {
  return (
    <>
    <MUIBox sx={style} {...props}>
      <div className='flex justify-between'>
        <Typography variant='h3' className="mb-[15px]">{ title ? title: '' }</Typography>
        <div className="mb-[20px]">
          {btn ? btn: ''}
        </div>
      </div>
      {children}
    </MUIBox>
    </>
  )
}

export default Box