import * as React from 'react';
import { styled } from '@mui/system';
import { ButtonBase } from '@mui/material';


const StyleBtn = styled(ButtonBase)(({ theme }) => ({
  minWidth: '70px',
  width: theme.button.common.width.md,
  height: theme.button.common.height.md,
  padding: theme.button.common.paddingSize.md,
  fontSize: theme.font.size.m,
  fontWeight: theme.font.weight.bold,
  color: theme.button.black.color,
  background: theme.button.black.background,
  border: `1px solid ${theme.button.black.border}`,
  borderRadius: theme.button.common.radius,
  transition: 'all 0.3s ease',
  whiteSpace: 'nowrap',
  '&:hover, &:active': {
    transform: 'scale(0.95)',
    background: theme.button.black.hover.background,
  },
  '&:disabled': {
    boxShadow: 'none',
    opacity: 0.5,
  },

}));

const BlackBtnMD = React.forwardRef((props, ref) => {
  return (
    <>
      <StyleBtn ref={ref} {...props} />
    </>
  );
});

export default BlackBtnMD;