import authApi from "../authApi";
import { FormDataConfig } from "../config";

export const getStudentInfo = async (studentId) => {
  const res = await authApi.get(`student/studentrelativeread/${studentId}`)
  return res.data[0]
}

// 원생 조회[manage/Student-3]
export const getStudentSpecInfo = async (studentId) => {
  const res = await authApi.get(`student/studentread/${studentId}`)
  return res.data[0]
}

// 도장, 반 별 원생 조회
export const getStudent = async (classes, dojangId) => {
  const res = await authApi.get(`student/studentreadbyclass/${dojangId}/${classes}`)
  return res.data
}

// 원생 수정
export const updateStudentSpecInfo = async (params) => {
  const res = await authApi.post(`student/studentupdate/${params[0]}`, params[1], FormDataConfig)
  return res
}

// 원생 <-> 반 추가
export const updateStudentClass = async (studentId, classId) => {
  const res = await authApi.post(`student/studentclassupdate/${classId}/${studentId}`)
  return res
}

// 원생 삭제
export const deleteStudent = async (studentId) => {
  const res = await authApi.put(`student/studentupdate/${studentId}`)
  return res
}

export const outStudent = async (dojangId, studentId) => {
  const res = await authApi.delete(`student/studentdischarge/${dojangId}/${studentId}`)
  return res
}

export const getStudentInfoForInvite = async (studentId) => {
  const res = await authApi.get(`student/studentread/${studentId}`)
  return res.data[0]
}

export const createStudent = async ([classId, body]) => {
  const res = await authApi.post(`student/studentcreate/${classId}`, body, FormDataConfig)
  return res
}

export const getExistStudentByInfo = async (param) => {
  const last_name = param?.last_name || ''
  const first_name = param?.first_name || ''
  const dob = param?.dob || ''
  const sex = param?.sex || ''
  const res = await authApi.get(`student/studentreadbybasic?student_lastname=${last_name}&student_firstname=${first_name}&student_ssn=${dob}&student_sex=${sex}`)
  return res.data
}

export const getExistStudentByCode = async (code) => {
  const res = await authApi.get(`student/studentreadbyunique/${code}`)
  return res.data
}

export const selectExistStudent = async ([studentId, body]) => {
  const res = await authApi.post(`student/studentupdate/${studentId}`, body)
  return res
}

