import * as React from 'react';
import { styled } from '@mui/system';
import { ButtonBase } from '@mui/material';

const StyleBtn = styled(ButtonBase)(({ theme }) => ({
  width: '140px',
  height: '40px',
  fontSize: theme.font.size.m,
  fontWeight: theme.font.weight.bold,
  color: theme.button.create.color,
  background: theme.button.create.background,
  border: `1px solid ${theme.button.create.border}`,
  borderRadius: theme.button.common.radius,
  padding: '5px 10px',
  transition: 'all 0.3s ease',
  whiteSpace: 'nowrap',
  '&:hover, &:active': {
    transform: 'scale(0.95)',
    boxShadow: theme.button.common.shadow.none,
    borderColor: theme.button.create.hover.border,
    background: theme.button.create.hover.background,
    color: theme.button.create.hover.color,
  },
  '&:disabled': {
    background: theme.button.create.disabled.background,
    color: theme.button.create.disabled.color,
    borderColor: theme.button.create.disabled.border,
    boxShadow: theme.button.common.shadow.none,
  },

}));

const CreateBtnMD = React.forwardRef((props, ref) => {
  return (
    <>
      <StyleBtn ref={ref} {...props} />
    </>
  );
});

export default CreateBtnMD;