import React from 'react';
import styled from 'styled-components';

const BoxStyle = styled.div`
  width: 375px;
  height: 345px;
  background-color: #fff;
  border-radius: 10px;
  padding-top: 20px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 0;
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  &:active {
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  }
  &:focus {
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  }
  div:first-child {
    font-size: 20px;
    font-weight: 700;
  }
  div:last-child {
    font-size: 17px;
    margin-top: 10px;
  }
  .selected {
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  }
  .selected:before {
    content: '';
    position: absolute;
    top: -20px;
    left: 50%;
    transform: translateX(-50%);
  }
`;

const SelectBox = ({ children, ...props }) => {
  return (
    <div className="flex justify-center items-center">
      <BoxStyle {...props}>{children}</BoxStyle>
    </div>
  );
};

export default SelectBox;
