import React from 'react'
import { useNavigate } from 'react-router-dom'
import BlackBtnMD from '../../../../../../components/UI/Atoms/Button/BlackBtnMD'
import EditBtnMD from '../../../../../../components/UI/Atoms/Button/EditBtnMD'
import { Typography } from '@mui/material'

const StepD = ({ setStep }) => {
  const navigate = useNavigate()
  return (
    <>
      <Typography variant="h3" className="mb-[50px]">
        초대장 발송이 완료되었습니다.
      </Typography>
      <div className="w-[500px] h-[200px] bg-[#fafafa] border border-solid border-[#dddddd] rounded-[5px] p-[20px]">
        <div className="text-center leading-7 mb-[30px] text-[16px]">
          보낸 초대장을 확인/취소 하시려면 <span className="font-bold">[초대장 관리]</span> 버튼을,{' '}
          <br />
          수련생 선택 화면으로 돌아가시려면 <br />
          <span className="font-bold">[수련생 선택]</span> 버튼을 눌러주세요.
        </div>
        <div className="flex justify-center">
          <EditBtnMD onClick={() => setStep(2)} className="mr-[5px]">
            수련생 선택
          </EditBtnMD>
          <BlackBtnMD onClick={() => navigate('../../invite/manage')}>초대장 관리</BlackBtnMD>
        </div>
      </div>
    </>
  );
}

export default StepD