import { useQuery } from '@tanstack/react-query'
import React, { useState } from 'react'
import { useRecoilValue } from 'recoil'
import { getStudent } from '../../../api/Student/studentApi'
import { useSelect } from '../../../hooks/basic/useSelect'
import dojangIdAtom from '../../../recoil/atom/dojangIdAtom'
import { phoneHyphenRegex } from '../../../utils/lib/Regex'
import { toAge } from '../../../utils/render/toAge'
import { toGender } from '../../../utils/render/toGender'
import EditBtnSM from '../../UI/Atoms/Button/EditBtnSM'
import Description from '../../UI/Atoms/Ex/Description'
import Em from '../../UI/Atoms/Ex/Em'
import ProfileImage from '../../UI/Atoms/Profile/ProfileImage'
import ClassSelectMD from '../Class/ClassSelectMD'
import { Grid } from '../Grid/Grid'
import RankImage from '../Rank/RankImage'

const SelectStudent = ({ setStep, setStudentId }) => {
  // recoil
  const dojangId = useRecoilValue(dojangIdAtom)

  // custom hook
  const [classId, handleChangeClasses] = useSelect('0')
  
  // query
  const { data: students } = useQuery(['student', classId, dojangId], () => getStudent(classId, dojangId), {
    enabled: !!dojangId && !!classId,
  })

  // handler
  const handleClickItem = e => {
    const clickId = e.target.id
    setStudentId(clickId)
    setStep()
  }

  return (
    <>
      <div className='flex justify-between items-center mb-3 w-full'>
        <ClassSelectMD value={classId} onChange={handleChangeClasses} />
      </div>

      { students && 
      <Grid>
        <Grid.Head>
          <Description>현재 선택된 반의 수련생은 <Em>{students?.count}명</Em>입니다.</Description>
        </Grid.Head>
        <Grid.RowWrap>
          {React.Children.toArray(
            students?.rows?.map((item) => 
              <Grid.Row>
                <Grid.Col w={1} className='pr-0'><ProfileImage urlKey={item?.photo_url}/></Grid.Col>
                <Grid.Col w={2}>{item?.last_name}{item?.first_name}</Grid.Col>
                <Grid.Col w={1}><RankImage src={item?.level} /></Grid.Col>
                <Grid.Col w={1}>{toGender(item?.sex)}</Grid.Col>
                <Grid.Col w={1} className='tracking-tight'>{toAge(item?.dob)}</Grid.Col>
                <Grid.Col w={2}>{item?.Classes[0]?.title}</Grid.Col>
                <Grid.Col w={2} className='tracking-tight'>{phoneHyphenRegex(item?.phone_number)}</Grid.Col>
                <Grid.Col>
                  <EditBtnSM id={item?.id} onClick={handleClickItem}>선택</EditBtnSM>
                </Grid.Col>
              </Grid.Row>
            )
          )}
        </Grid.RowWrap>
      </Grid>
      }

    </>
  )
}

export default SelectStudent