import { Typography } from '@mui/material'
import { useMutation } from '@tanstack/react-query'
import React, { useState } from 'react'
import { useRecoilValue } from 'recoil'
import { sendInvitationTeacher } from '../../../../../api/Invite/inviteApi'
import Box from '../../../../../components/layout/Box'
import CreateBtnMD from '../../../../../components/UI/Atoms/Button/CreateBtnMD'
import EditBtnSM from '../../../../../components/UI/Atoms/Button/EditBtnSM'
import MinusIconBtn from '../../../../../components/UI/Atoms/Button/MinusIconBtn'
import InfoEditInput from '../../../../../components/UI/Atoms/Input/InfoEditInput'
import BasicSelect from '../../../../../components/UI/Atoms/Select/Basic/BasicSelect'
import LoadingModal from '../../../../../components/UI/Atoms/Spinner/LoadingModal'
import { useDebug } from '../../../../../hooks/basic/useDebug'
import dojangIdAtom from '../../../../../recoil/atom/dojangIdAtom'

const InviteSabum = () => {
  const dojangId = useRecoilValue(dojangIdAtom)
  const initRow = { recipient_type: '', phone_number: '' }
  const [data, setData] = useState([initRow])

  // query
  const { mutate: inviteMutation, isLoading } = useMutation(['sendInvitationSabum',], (body) => sendInvitationTeacher(body))

  // handler
  const handleAddRow = e => {
    if (data.length < 10) {
      setData([...data, initRow])
    } else {
      alert('초대장은 한번에 10개만 보낼 수 있습니다.')
    }
  }

  const handleDelRow = e => {
    let {id} = e.currentTarget
    let temp = data.filter((v, idx) => { return id != idx })
    setData(temp)
  }

  const handleChange = e => {
    let {id, name, value} = e.currentTarget
    let temp = [...data]
    temp[id] = {...temp[id], [name]:value}
    setData(temp)
  }

  const handleSelect = (e, idx) => {
    let {name, value} = e.target
    let temp = [...data]
    temp[idx] = {
      ...temp[idx], 
      [name]: value,
    }
    setData(temp)
  }

  const handleSubmit = e => {
    let temp = data.map(item => {
      return {
        ...item,
        recipient_type: item?.recipient_type === '0' ? '관장' : '사범',
      }
    })
    let body = {
      dojang_id: dojangId,
      invite_info: temp,
    }
    // console.log(body)
    inviteMutation(body, {
      onSuccess: () => {
        alert('초대장 발송이 성공하였습니다. \n보낸 초대장 확인은 "초대장 관리"에서 할 수 있습니다. ')
        window.location.reload()
      }
    })
  }

  // useDebug(data)

  return (
      <>
      { isLoading && <LoadingModal />}
        <Typography variant='h3' className='mb-[10px] text-center'>관장님, 사범님 초대하기</Typography>
        <Box className='w-[500px] p-0 m-0'>
          <div className='flex justify-end mb-[10px] mr-[29px]'>
            <EditBtnSM onClick={handleAddRow} className='w-[80px]'>+ 초대 추가</EditBtnSM>
          </div>
        </Box>
        <div className='flex flex-col gap-[10px] w-[500px]'>
          { React.Children.toArray(
            data?.map( (item, idx) =>
            <div className='flex justify-start gap-[5px]'>
                <BasicSelect 
                  height='30px' 
                  name='recipient_type' 
                  value={item?.recipient_type} 
                  onChange={(e)=>handleSelect(e,idx)} 
                  dataList={['관장','사범']}
                  width='140px'
                  className='text-[14px]'
                />
                <InfoEditInput 
                  id={`${idx}`} 
                  name='phone_number' 
                  value={item?.phone_number}
                  onChange={handleChange} 
                  placeholder='전화번호 입력'
                  length={326}
                  className=''
                />
                { idx !== 0 &&
                  <MinusIconBtn id={`${idx}`} onClick={handleDelRow} />
                }
              </div>
            )
          )}
        </div>
        <div className='flex justify-center mt-[30px]'>
          <CreateBtnMD onClick={handleSubmit}>초대장 보내기</CreateBtnMD>
        </div>
      </>
  )
}

export default InviteSabum