import authApi from "../authApi"

export const sendInvitation = async (body) => {
  const res = await authApi.post(`util/invite_user_multi/`, body)
  return res
}

export const sendInvitationTeacher = async (body) => {
  const res = await authApi.post(`util/invite_teacher_multi/`, body)
  return res
}


export const getInvitationList = async (dojangId) => {
  const res = await authApi.get(`dojang/inviteread/${dojangId}`)
  return res.data
}

export const cancleInvitation = async (inviteId) => {
  const res = await authApi.delete(`user/invitedelete/${inviteId}`)
  return res
}

export const resendInvitation = async (id) => {
  // const res = await authApi.get(`/${id}`)
  return ''
}

export const getInvitedInfo = async () => {
  const res = await authApi.get('user/is_invited')
  return res.data
}

export const acceptInvite = async (inviteId) => {
  const res = await authApi.post(`user/inviteaccept/${inviteId}`)
  return res
}

export const refuseInvite = async (inviteId) => {
  const res = await authApi.post(`user/inviterefuse/${inviteId}`)
  return res
}
