import React from 'react'
import { useSetRecoilState } from 'recoil'
import SelectStudent from '../../../../../../components/common/Student/SelectStudent'
import { inviteStudentIdAtom } from '../recoil/InviteStudentIdAtom'

const StepB = ({ setStep }) => {

  // recoil
  const setStudentId = useSetRecoilState(inviteStudentIdAtom)

  return (
    <>
      <SelectStudent setStep={() => setStep(3)} setStudentId={setStudentId} />
    </>
  )
}

export default StepB