import React from 'react'
import Select from './Select'
import {MenuItem as MUIItem} from '@mui/material';
import { MenuItem, Select as MUISelect } from '@mui/material';

const selectItemStyle = {
  width: '100%',
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  '&:hover': {
    backgroundColor: '#f3f3f3',
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
  '&:focus': {
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
}

const BasicSelect = ({ width, height, value, onChange, dataList, ...props }) => {
  

  return (
    <MUISelect
      sx={{width: `${width || '140px'}`, height: `${height || '40px'}`}}
      size='small'
      value={value}
      onChange={onChange}

      {...props}
    >
      { React.Children.toArray(
        dataList?.map( (item, idx) =>
          <MUIItem value={`${idx}`} sx={selectItemStyle}>
            {item}
          </MUIItem>
        )
      )}
    </MUISelect>
  )
}

export default BasicSelect