import React, { useState, useRef } from 'react';
import PageContainer from '../../../../components/layout/PageContainer';
import EditBtnSM from '../../../../components/UI/Atoms/Button/EditBtnSM';
import InviteSabum from './InviteSabum/InviteSabum';
import InviteStudent from './InviteStudent/InviteStudent';
import InviteTypeSelect from './InviteTypeSelect/InviteTypeSelect';
import { Typography } from '@mui/material';
import TelegramIcon from '@mui/icons-material/Telegram';
import EditBtnMD from '../../../../components/UI/Atoms/Button/EditBtnMD';
import PrimaryBtnMD from '../../../../components/UI/Atoms/Button/PrimaryBtnLG';

const InviteSendPage = () => {
  const [step, setStep] = useState(1);
  const [selectType, setSelectType] = useState('');

  // handler
  const handleClickItem = (e) => {
    const clickId = e.currentTarget.id;
    setSelectType(clickId);
  };

  const src = {
    student: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/invite/student_parents.png',
    teacher: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/invite/teacher.png',
  };

  return (
    <PageContainer>
      {step === 1 && (
        <>
          <Typography variant="h3">유형을 선택하세요.</Typography>
          <div className="flex justify-between items-center gap-[20px]">
            <InviteTypeSelect
              id="1"
              title="수련생 & 학부모"
              onClick={handleClickItem}
              className={selectType === '1' && 'type_selected'}
              src={src.student}
            >
              수련생, 학부모, 가족들을 초대합니다.
            </InviteTypeSelect>
            <InviteTypeSelect
              id="2"
              title="관장 & 사범"
              onClick={handleClickItem}
              className={selectType === '2' && 'type_selected'}
              src={src.teacher}
            >
              관장님, 사범님을 초대합니다.
            </InviteTypeSelect>
          </div>
          <div className="flex w-[770px] justify-center bg-[#f2f2f2] rounded-[10px] my-[20px] border border-solid border-[1px] border-[#e7e7e7]">
            <TelegramIcon className="mt-[12px] mr-[20px] text-[#555555] w-[40px] h-[40px]" />
            <span className="py-[10px]">
              초대장은 7일 후 자동 삭제 됩니다. <br />
              초대장 발송을 위해 <u className="underline-offset-4 font-bold">최소 1개의 반</u>이
              존재 해야 합니다.
            </span>
          </div>
          <div className="flex justify-center">
            <PrimaryBtnMD onClick={() => setStep(2)} className='w-[140px] h-[40px] text-[17px]' disabled={!selectType}>
              다음
            </PrimaryBtnMD>
          </div>
        </>
      )}
      {step === 2 && selectType === '1' && <InviteStudent />}

      {step === 2 && selectType === '2' && <InviteSabum />}
    </PageContainer>
  );
};

export default InviteSendPage;
