export function toAge(dob) {
  if (!dob) return '';

  let today = new Date();
  let dobDate = new Date(dob);
  let currYear = today.getFullYear();
  let dobYear = dobDate.getFullYear();
  let age = currYear - dobYear;
  let thisBirth = dobDate.setFullYear(currYear);
  if (today.getTime() > thisBirth) {
    return `${age--}살`;
  }
  return `${age}살`;
}
